<template>
  <div class="statistic__templs">
    <div class="templs-table__head">
      <div class="templs-table-head__templ">
        <font-awesome-icon :icon="['fas', 'church']" />
      </div>
      <div class="templs-table-head__all">
        <font-awesome-icon :icon="['fas', 'square-poll-vertical']" />
      </div>
      <div class="templs-table-head__2b">
        <font-awesome-icon :icon="['fas', 'hospital']" />
      </div>
      <div class="templs-table-head__cemetery">
        <font-awesome-icon :icon="['fas', 'cross']" />
      </div>
      <div class="templs-table-head__adress">
        <font-awesome-icon :icon="['fas', 'house']" />
      </div>
    </div>
    <div class="templs-table__body" v-for="templ in statTempls" :key="templ.id">
      <div class="templs-table-body__templ">{{ templ.name }}</div>
      <div
        class="templs-table-body__all"
        :class="{ 'templs-stat-green': templ.diffFun <= 0 }"
      >
        {{ templ.allFun }}
      </div>
      <div class="templs-table-body__2b">{{ templ.all2b }}</div>
      <div class="templs-table-body__cemetery">{{ templ.allCemetery }}</div>
      <div class="templs-table-body__adress">{{ templ.allAddress }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "StatisticTempls",
  props: ["funerals"],
  computed: {
    statTempls() {
      let templs = this.$store.getters.GET_TEMPLS;
      let templStat = [];
      let fun = this.$props.funerals;

      templs.forEach((i) => {
        let allFun = 0,
          all2b = 0,
          allCemetery = 0,
          allAddress = 0;

        fun.forEach((fun) => {
          if (fun.templ == i.name) {
            allFun = allFun + 1;
            if (fun.locationTempl == "-  ( 2)") {
              all2b = all2b + 1;
            }
            allCemetery = fun.locationCemetery ? allCemetery++ : allCemetery;
            allAddress = fun.locationAddress ? allAddress++ : allAddress;
          }
        });

        let obj = {
          name: i.name,
          allFun,
          all2b,
          allCemetery,
          allAddress,
          diffFun: i.minFun - allFun,
        };

        templStat.push(obj);
      });

      return templStat;
    },
  },
};
</script>

<style lang="scss">
.templs-table__head {
  width: 100%;
  height: 40px;
  background: $col-bg-hover;
  position: sticky;
  top: -5px;
  display: flex;
  flex-wrap: nowrap;
  font-size: 1.3em;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 5px;
  & .templs-table-head__templ {
    width: 60%;
  }
  & .templs-table-head__all,
  .templs-table-head__2b,
  .templs-table-head__cemetery,
  .templs-table-head__adress {
    width: 10%;
  }
  & .templs-table-head__all {
    color: $col-basic;
  }
}
.templs-table__body {
  width: 100%;
  background: $col-bg-dark-hover;
  display: flex;
  flex-wrap: nowrap;
  font-size: 1.1em;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-bottom: 5px;
  & .templs-table-body__templ {
    width: 60%;
    text-align: left;
    padding: 5px;
  }
  & .templs-table-body__all,
  .templs-table-body__2b,
  .templs-table-body__cemetery,
  .templs-table-body__adress {
    width: 10%;
    &.templs-stat-green {
      color: $col-green;
    }
  }
  & .templs-table-body__all {
    color: $col-basic;
  }
}
</style>