<template>
  <div class="app__statistic">
    <h1>{{ getDate }}</h1>
    <div class="statistic__content">
      <transition name="slide">
        <statistic-templs v-if="statMenu == 'templ'" :funerals="getStatistic" />
        <statistic-services
          v-else-if="statMenu == 'services'"
          :funerals="getStatistic"
        />
        <statistic-funerals
          v-else-if="statMenu == 'allstat'"
          :funerals="getStatistic"
        />
      </transition>
    </div>
    <statistic-menu @statMenu="(e) => (statMenu = e)" />
  </div>
</template>

<script>
import StatisticMenu from "@/components/AppStatistic/StatisticMenu.vue";
import StatisticTempls from "@/components/AppStatistic/StatisticTempls.vue";
import StatisticServices from "@/components/AppStatistic/StatisticServices.vue";
import StatisticFunerals from "@/components/AppStatistic/StatisticFunerals.vue";

export default {
  components: {
    StatisticMenu,
    StatisticTempls,
    StatisticServices,
    StatisticFunerals,
  },
  name: "AppFunStatistic",
  data() {
    return {
      statMenu: "templ",
    };
  },
  computed: {
    getDate() {
      this.$store.dispatch("SET_FUNERALS_STATISTIC");
      return this.$store.getters.GET_DATE_STATISTIC;
    },
    getStatistic() {
      return this.$store.getters.GET_FUNERALS_STATISTIC;
    },
  },
};
</script>

<style lang="scss">
.app__statistic {
  width: 100%;
  height: calc(100dvh - 16dvh);
  position: relative;
  overflow: hidden;
  .statistic__content {
    width: 100%;
    height: calc(100dvh - 30dvh);
    margin-bottom: 5px;
    overflow: auto;
    position: relative;
  }
  .statistic__menu {
    width: 100%;
    height: calc(8dvh - 10px);
    display: flex;
    font-size: 1.7em;
    line-height: calc(8dvh - 15px);
    text-align: center;
  }
}
</style>